* {
  box-sizing: border-box;
}

main {
  max-width: 40vw;
  width: 100%;
  min-width: 400px;
  margin: 0 auto;
}
main h1 {
  text-align: center;
  margin-bottom: 0.4em;
}

.articles {
  text-align: center;
  font-style: italic;
}

.articles-info {
  display: inline-block;
  color: rgba(128, 141, 148, 0.82);
  margin: 10px 0 3px 0;
}

.pagination-wrapper {
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
  position: relative;
}

ul.pagination-pages li {
  display: inline-block;
}

ul.pagination-pages li span {
  cursor: default;
  letter-spacing: 1px;
}

a.page-link {
  display: inline-block;
  text-decoration: none;
  color: #4285f4;
  padding: 2px 5px 2px 5px;
  border: 1px solid;
  border-radius: 2px;
  margin: 0px 1px;
}

a.page-link:hover {
  text-decoration: underline;
}

a.page-link__active {
  color: #3b4a33;
  cursor: default;
}

a.page-link__active:hover {
  text-decoration: none;
}

a.page-link__disable {
  color: #888d86;
  cursor: default;
}

a.page-link__disable:hover {
  text-decoration: none;
}

.pages-select {
  padding: 3px 0px 2px 0px;
  position: absolute;
  border-radius: 2px;
  right: -50px;
}
